'use client';

import React, { useState, useEffect, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import BarcodeManager from '@/lib/BarcodeManager';
import ExportUtils from '@/lib/ExportUtils';

interface Product {
  id?: number;
  name: string;
  category: string;
  brand: string;
  model: string;
  storage: string;
  condition: 'new' | 'used';
  carrier: string;
  price: number;
  barcode: string;
  barcodeImage?: string;
}

interface AppStats {
  products: number;
  categories: number;
  projects: number;
}

export default function PriceSheetCreator() {
  // State management
  const [activeTab, setActiveTab] = useState('dashboard');
  const [products, setProducts] = useState<Product[]>([]);
  const [currentProduct, setCurrentProduct] = useState<Product>({
    name: '',
    category: '',
    brand: '',
    model: '',
    storage: '',
    condition: 'new',
    carrier: '',
    price: 0,
    barcode: ''
  });
  const [stats, setStats] = useState<AppStats>({ products: 0, categories: 0, projects: 0 });
  const [isScanning, setIsScanning] = useState(false);
  const [scanResult, setScanResult] = useState('');
  const [alert, setAlert] = useState<{ type: 'success' | 'error' | 'info'; message: string } | null>(null);
  const [isElectron, setIsElectron] = useState(false);

  // Refs
  const scanInputRef = useRef<HTMLInputElement>(null);
  const barcodeManager = useRef(new BarcodeManager());
  const exportUtils = useRef(new ExportUtils());

  // Categories
  const categories = [
    'Network Plans',
    'Smartphones', 
    'Accessories',
    'Laptops',
    'Tablets'
  ];

  const carriers = [
    'Unlocked',
    'Verizon',
    'AT&T',
    'T-Mobile',
    'Sprint',
    'Other'
  ];

  // Initialize app
  useEffect(() => {
    setIsElectron(typeof window !== 'undefined' && !!(window as any).electronAPI);
    loadInitialData();
    
    // Focus scan input for barcode scanner
    if (scanInputRef.current) {
      scanInputRef.current.focus();
    }
  }, []);

  const loadInitialData = () => {
    // Load sample data for demo
    const sampleProducts: Product[] = [
      {
        id: 1,
        name: 'iPhone 15 Pro',
        category: 'Smartphones',
        brand: 'Apple',
        model: '15 Pro',
        storage: '256GB',
        condition: 'new',
        carrier: 'Unlocked',
        price: 999.99,
        barcode: 'PS001'
      },
      {
        id: 2,
        name: 'Samsung Galaxy S24',
        category: 'Smartphones',
        brand: 'Samsung',
        model: 'Galaxy S24',
        storage: '128GB',
        condition: 'new',
        carrier: 'Verizon',
        price: 799.99,
        barcode: 'PS002'
      }
    ];

    setProducts(sampleProducts);
    setStats({ products: sampleProducts.length, categories: 5, projects: 0 });
  };

  const showAlert = (type: 'success' | 'error' | 'info', message: string) => {
    setAlert({ type, message });
    setTimeout(() => setAlert(null), 5000);
  };

  const generateBarcode = () => {
    const code = barcodeManager.current.generateProductCode();
    setCurrentProduct(prev => ({ ...prev, barcode: code }));
  };

  const addProduct = () => {
    if (!currentProduct.name || !currentProduct.price) {
      showAlert('error', 'Please fill in required fields (Name and Price)');
      return;
    }

    const newProduct: Product = {
      ...currentProduct,
      id: Date.now(),
      barcode: currentProduct.barcode || barcodeManager.current.generateProductCode()
    };

    // Generate barcode image
      try {
        const barcodeImage = barcodeManager.current.generateBarcodeDataURL(newProduct.barcode);
        if (barcodeImage) {
          newProduct.barcodeImage = barcodeImage;
        }
      } catch (error) {
        console.error('Barcode generation error:', error);
      }

    setProducts(prev => [...prev, newProduct]);
    setStats(prev => ({ ...prev, products: prev.products + 1 }));
    
    // Reset form
    setCurrentProduct({
      name: '',
      category: '',
      brand: '',
      model: '',
      storage: '',
      condition: 'new',
      carrier: '',
      price: 0,
      barcode: ''
    });

    showAlert('success', 'Product added successfully!');
  };

  const removeProduct = (id: number) => {
    setProducts(prev => prev.filter(p => p.id !== id));
    setStats(prev => ({ ...prev, products: prev.products - 1 }));
    showAlert('info', 'Product removed');
  };

  const clearAllProducts = () => {
    setProducts([]);
    setStats(prev => ({ ...prev, products: 0 }));
    showAlert('info', 'All products cleared');
  };

  const handleScanInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    const scannedCode = e.target.value.trim();
    if (scannedCode) {
      setScanResult(scannedCode);
      
      // Look for existing product
      const existingProduct = products.find(p => p.barcode === scannedCode);
      if (existingProduct) {
        showAlert('info', `Found: ${existingProduct.name} - $${existingProduct.price}`);
      } else {
        setCurrentProduct(prev => ({ ...prev, barcode: scannedCode }));
        showAlert('info', `Scanned code: ${scannedCode}`);
      }
      
      // Clear input
      e.target.value = '';
    }
  };

  const exportPriceSheet = async () => {
    try {
      const result = await exportUtils.current.exportPriceSheet('price-sheet-preview');
      if (result.success) {
        showAlert('success', 'Price sheet exported successfully!');
      } else {
        showAlert('error', `Export failed: ${result.error}`);
      }
    } catch (error) {
      showAlert('error', 'Export failed');
    }
  };

  const printLabels = async () => {
    try {
      const result = await exportUtils.current.printLabels(products);
      if (result.success) {
        showAlert('success', 'Labels sent to printer!');
      } else {
        showAlert('error', `Print failed: ${result.error}`);
      }
    } catch (error) {
      showAlert('error', 'Print failed');
    }
  };

  const saveProject = async () => {
    const projectData = {
      name: `Project ${new Date().toLocaleDateString()}`,
      description: 'Price sheet project',
      data: { products, stats }
    };

    try {
      const result = await exportUtils.current.saveProject(projectData);
      if (result.success) {
        showAlert('success', 'Project saved successfully!');
      } else {
        showAlert('error', `Save failed: ${result.error}`);
      }
    } catch (error) {
      showAlert('error', 'Save failed');
    }
  };

  const loadProject = async () => {
    try {
      const result = await exportUtils.current.loadProject();
      if (result.success && result.data) {
        setProducts(result.data.products || []);
        setStats(result.data.stats || { products: 0, categories: 5, projects: 0 });
        showAlert('success', 'Project loaded successfully!');
      } else {
        showAlert('error', `Load failed: ${result.error}`);
      }
    } catch (error) {
      showAlert('error', 'Load failed');
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex">
      {/* Hidden input for barcode scanner */}
      <input
        ref={scanInputRef}
        type="text"
        className="absolute opacity-0 pointer-events-none"
        onChange={handleScanInput}
        autoFocus
      />

      {/* Sidebar */}
      <div className="w-64 bg-white shadow-lg border-r">
        <div className="p-6 border-b">
          <h1 className="text-xl font-bold text-gray-900">Price Sheet Creator</h1>
          <p className="text-sm text-gray-500 mt-1">Phone Store Management</p>
        </div>

        <nav className="p-4">
          <div className="space-y-2">
            <button
              onClick={() => setActiveTab('dashboard')}
              className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                activeTab === 'dashboard' 
                  ? 'bg-blue-100 text-blue-700 font-medium' 
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              Dashboard
            </button>
            <button
              onClick={() => setActiveTab('create')}
              className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                activeTab === 'create' 
                  ? 'bg-blue-100 text-blue-700 font-medium' 
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              Create Sheet
            </button>
            <button
              onClick={() => setActiveTab('projects')}
              className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                activeTab === 'projects' 
                  ? 'bg-blue-100 text-blue-700 font-medium' 
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              Projects
            </button>
            <button
              onClick={() => setActiveTab('settings')}
              className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                activeTab === 'settings' 
                  ? 'bg-blue-100 text-blue-700 font-medium' 
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              Settings
            </button>
          </div>
        </nav>

        {/* Stats */}
        <div className="p-4 border-t mt-auto">
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-500">Products:</span>
              <span className="font-medium">{stats.products}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500">Categories:</span>
              <span className="font-medium">{stats.categories}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500">Projects:</span>
              <span className="font-medium">{stats.projects}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 p-6">
        {/* Alert */}
        {alert && (
          <Alert className={`mb-4 ${
            alert.type === 'error' ? 'border-red-200 bg-red-50' :
            alert.type === 'success' ? 'border-green-200 bg-green-50' :
            'border-blue-200 bg-blue-50'
          }`}>
            <AlertDescription className={
              alert.type === 'error' ? 'text-red-700' :
              alert.type === 'success' ? 'text-green-700' :
              'text-blue-700'
            }>
              {alert.message}
            </AlertDescription>
          </Alert>
        )}

        {/* Dashboard Tab */}
        {activeTab === 'dashboard' && (
          <div className="space-y-6">
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-2">Dashboard</h2>
              <p className="text-gray-600">Overview of your price sheet data</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-500">Total Products</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.products}</div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-500">Categories</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.categories}</div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-500">Projects</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.projects}</div>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>Recent Products</CardTitle>
              </CardHeader>
              <CardContent>
                {products.length > 0 ? (
                  <div className="space-y-2">
                    {products.slice(-5).map(product => (
                      <div key={product.id} className="flex justify-between items-center p-2 bg-gray-50 rounded">
                        <div>
                          <span className="font-medium">{product.name}</span>
                          <span className="text-gray-500 ml-2">{product.brand} {product.model}</span>
                        </div>
                        <span className="font-bold">${product.price}</span>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-gray-500">No products added yet</p>
                )}
              </CardContent>
            </Card>
          </div>
        )}

        {/* Create Sheet Tab */}
        {activeTab === 'create' && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Input Form */}
            <Card>
              <CardHeader>
                <CardTitle>Add Product</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="name">Product Name *</Label>
                    <Input
                      id="name"
                      value={currentProduct.name}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, name: e.target.value }))}
                      placeholder="iPhone 15 Pro"
                    />
                  </div>
                  <div>
                    <Label htmlFor="category">Category</Label>
                    <Select value={currentProduct.category} onValueChange={(value) => setCurrentProduct(prev => ({ ...prev, category: value }))}>
                      <SelectTrigger>
                        <SelectValue placeholder="Select category" />
                      </SelectTrigger>
                      <SelectContent>
                        {categories.map(cat => (
                          <SelectItem key={cat} value={cat}>{cat}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="brand">Brand</Label>
                    <Input
                      id="brand"
                      value={currentProduct.brand}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, brand: e.target.value }))}
                      placeholder="Apple"
                    />
                  </div>
                  <div>
                    <Label htmlFor="model">Model</Label>
                    <Input
                      id="model"
                      value={currentProduct.model}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, model: e.target.value }))}
                      placeholder="15 Pro"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="storage">Storage</Label>
                    <Input
                      id="storage"
                      value={currentProduct.storage}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, storage: e.target.value }))}
                      placeholder="256GB"
                    />
                  </div>
                  <div>
                    <Label htmlFor="condition">Condition</Label>
                    <Select value={currentProduct.condition} onValueChange={(value: 'new' | 'used') => setCurrentProduct(prev => ({ ...prev, condition: value }))}>
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="new">New</SelectItem>
                        <SelectItem value="used">Used</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="carrier">Carrier</Label>
                    <Select value={currentProduct.carrier} onValueChange={(value) => setCurrentProduct(prev => ({ ...prev, carrier: value }))}>
                      <SelectTrigger>
                        <SelectValue placeholder="Select carrier" />
                      </SelectTrigger>
                      <SelectContent>
                        {carriers.map(carrier => (
                          <SelectItem key={carrier} value={carrier}>{carrier}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label htmlFor="price">Price *</Label>
                    <Input
                      id="price"
                      type="number"
                      step="0.01"
                      value={currentProduct.price || ''}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, price: parseFloat(e.target.value) || 0 }))}
                      placeholder="999.99"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="barcode">Barcode</Label>
                  <div className="flex gap-2">
                    <Input
                      id="barcode"
                      value={currentProduct.barcode}
                      onChange={(e) => setCurrentProduct(prev => ({ ...prev, barcode: e.target.value }))}
                      placeholder="Auto-generated or scanned"
                    />
                    <Button type="button" variant="outline" onClick={generateBarcode}>
                      Generate
                    </Button>
                  </div>
                </div>

                <div className="flex gap-2">
                  <Button onClick={addProduct} className="flex-1">
                    Add Product
                  </Button>
                  <Button variant="outline" onClick={clearAllProducts}>
                    Clear All
                  </Button>
                </div>
              </CardContent>
            </Card>

            {/* Price Sheet Preview */}
            <Card>
              <CardHeader className="flex flex-row items-center justify-between">
                <CardTitle>Price Sheet Preview</CardTitle>
                <div className="flex gap-2">
                  <Button variant="outline" size="sm" onClick={exportPriceSheet}>
                    Export
                  </Button>
                  <Button variant="outline" size="sm" onClick={printLabels}>
                    Print Labels
                  </Button>
                </div>
              </CardHeader>
              <CardContent>
                <div id="price-sheet-preview" className="bg-white p-4 border rounded">
                  <div className="text-center mb-4">
                    <h3 className="text-lg font-bold">Price Sheet</h3>
                    <p className="text-sm text-gray-500">{new Date().toLocaleDateString()}</p>
                  </div>
                  
                  {products.length > 0 ? (
                    <div className="space-y-2">
                      {products.map(product => (
                        <div key={product.id} className="flex items-center justify-between p-2 border-b">
                          <div className="flex-1">
                            <div className="font-medium">{product.name}</div>
                            <div className="text-sm text-gray-500">
                              {product.brand} {product.model} {product.storage}
                              {product.condition === 'used' && <Badge variant="secondary" className="ml-2">Used</Badge>}
                            </div>
                            <div className="text-xs text-gray-400">{product.barcode}</div>
                          </div>
                          <div className="text-right">
                            <div className="font-bold">${product.price}</div>
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => removeProduct(product.id!)}
                              className="text-red-500 hover:text-red-700"
                            >
                              Remove
                            </Button>
                          </div>
                        </div>
                      ))}
                      <div className="pt-2 border-t font-bold text-right">
                        Total Items: {products.length}
                      </div>
                    </div>
                  ) : (
                    <p className="text-center text-gray-500 py-8">No products added yet</p>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>
        )}

        {/* Projects Tab */}
        {activeTab === 'projects' && (
          <div className="space-y-6">
            <div className="flex justify-between items-center">
              <div>
                <h2 className="text-2xl font-bold text-gray-900 mb-2">Projects</h2>
                <p className="text-gray-600">Manage your saved price sheet projects</p>
              </div>
              <div className="flex gap-2">
                <Button onClick={saveProject}>Save Current</Button>
                <Button variant="outline" onClick={loadProject}>Load Project</Button>
              </div>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>Project Management</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-500">
                  {isElectron 
                    ? 'Use the buttons above to save your current work or load a previously saved project.'
                    : 'Project files will be downloaded/uploaded as JSON files in your browser.'
                  }
                </p>
              </CardContent>
            </Card>
          </div>
        )}

        {/* Settings Tab */}
        {activeTab === 'settings' && (
          <div className="space-y-6">
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-2">Settings</h2>
              <p className="text-gray-600">Application settings and preferences</p>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>Application Info</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <span className="text-gray-500">Environment:</span>
                    <span className="ml-2 font-medium">{isElectron ? 'Desktop (Electron)' : 'Web Browser'}</span>
                  </div>
                  <div>
                    <span className="text-gray-500">Version:</span>
                    <span className="ml-2 font-medium">1.0.0</span>
                  </div>
                  <div>
                    <span className="text-gray-500">Barcode Scanner:</span>
                    <span className="ml-2 font-medium">USB Scanner Ready</span>
                  </div>
                  <div>
                    <span className="text-gray-500">Export:</span>
                    <span className="ml-2 font-medium">{isElectron ? 'Native File Dialog' : 'Browser Download'}</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Barcode Scanner Test</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <p className="text-sm text-gray-600">
                    Scan a barcode or type manually to test the scanner functionality.
                  </p>
                  <div>
                    <Label>Last Scanned Code:</Label>
                    <div className="mt-1 p-2 bg-gray-100 rounded font-mono">
                      {scanResult || 'No code scanned yet'}
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        )}
      </div>
    </div>
  );
}
